.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: rgba(0, 16, 61, 0.84);
}

.hidden {
  transform: translateX(100%);
  transition: transform 0.5s ease-in-out;
}

.visible {
  transform: translateX(0);
  transition: transform 0.5s ease-in-out;
}

*, *::before, *::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  color: #252b42;
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
  margin: 0;
  padding: 0;
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

img {
  max-width: 100%;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
ul li {
  font-size: 1rem;
}

a, li {
  text-decoration: none;
}

.heading {
  font-weight: bold;
  margin: 15px 0;
}
.heading--1 {
  font-size: 40px;
  line-height: 47px;
}
@media (min-width: 1280px) {
  .heading--1 {
    font-size: 3.25rem;
    line-height: 61px;
  }
}
.heading--2 {
  font-size: 2rem;
  line-height: 38px;
}
@media (min-width: 1280px) {
  .heading--2 {
    font-size: 2.75rem;
    line-height: 52px;
  }
}
.heading--3 {
  font-size: 26px;
}
@media (min-width: 1280px) {
  .heading--3 {
    font-size: 2rem;
  }
}

.label {
  display: block;
  margin-bottom: 30px;
  font-size: 1.5rem;
  color: #5956E9;
  font-weight: 700;
  line-height: 36px;
  text-align: center;
}
@media (min-width: 992px) {
  .label {
    text-align: start;
    margin-bottom: 15px;
  }
}

.container {
  max-width: 1054px;
  margin: 0 auto;
  padding: 0 15px;
  overflow: visible;
}
.container--full {
  max-width: 1440px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.grid--3 {
  grid-template-columns: repeat(3, 1fr);
}
.grid--4 {
  grid-template-columns: repeat(4, 1fr);
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.col {
  width: 100%;
  padding: 15px;
  display: inline-block;
}
@media (min-width: 992px) {
  .col--md25 {
    flex: 0 0 25%;
    max-width: 25%;
  }
}
@media (min-width: 992px) {
  .col--md33 {
    flex: 0 0 33.3%;
    max-width: 33.3%;
  }
}
@media (min-width: 992px) {
  .col--md50 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
@media (min-width: 1280px) {
  .col--lg25 {
    flex: 0 0 25%;
    max-width: 25%;
  }
}
@media (min-width: 1280px) {
  .col--lg33 {
    flex: 0 0 33.3%;
    max-width: 33.3%;
  }
}
@media (min-width: 1280px) {
  .col--lg50 {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
.col--hidden-sm {
  display: none;
}
@media (min-width: 536px) {
  .col--hidden-sm {
    display: inline-block;
  }
}

.header {
  padding-top: 25px;
  position: relative;
}
@media (min-width: 992px) {
  .header {
    padding-top: 60px;
  }
}
.header .flex {
  justify-content: space-between;
}
.header__btn.btn {
  display: none;
}
@media (min-width: 992px) {
  .header__btn.btn {
    display: inline-block;
  }
}

.section {
  padding: 65px 0 0;
}
@media (min-width: 992px) {
  .section {
    padding: 150px 0 0;
  }
}

.showcase {
  overflow: hidden;
}
.showcase__text {
  text-align: center;
  margin-top: 69px;
}
@media (min-width: 992px) {
  .showcase__text {
    text-align: left;
    width: 60%;
    margin-top: 100px;
  }
}
.showcase__text h1 {
  margin-bottom: 24px;
}
@media (min-width: 992px) {
  .showcase__text h1 {
    margin-bottom: 49px;
  }
}
.showcase__text p {
  margin-bottom: 44px;
  color: #868686;
  font-size: 1.5rem;
  line-height: 38px;
}
@media (min-width: 992px) {
  .showcase__text p {
    margin-bottom: 62px;
  }
}
.showcase__img {
  z-index: -1;
}
.showcase__img--desktop {
  display: none;
}
@media (min-width: 536px) {
  .showcase__img--desktop {
    display: inline-block;
    position: relative;
    right: -15px;
    margin-top: 30px;
  }
}
@media (min-width: 992px) {
  .showcase__img--desktop {
    position: absolute;
    top: 0;
    right: 0;
    width: 45%;
    margin-top: unset;
  }
}
.showcase__img--mobile {
  display: inline-block;
  position: relative;
  right: -15px;
  margin-top: 60px;
}
@media (min-width: 536px) {
  .showcase__img--mobile {
    display: none;
  }
}
.showcase .flex {
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 992px) {
  .showcase .flex {
    flex-direction: row;
  }
}

.application {
  padding-top: 190px;
}
.application__info {
  font-weight: 600;
  font-size: 34px;
  line-height: 36px;
  margin-bottom: 15px;
}
@media (min-width: 992px) {
  .application__info {
    font-size: 2.75rem;
  }
}
.application__text {
  text-align: center;
}
@media (min-width: 992px) {
  .application__text {
    text-align: left;
    margin-left: 60px;
  }
}
.application__text p {
  margin-top: 34px;
  color: #868686;
  font-size: 1.5rem;
  line-height: 38px;
}
.application__img--desktop {
  display: none;
}
@media (min-width: 992px) {
  .application__img--desktop {
    display: inline-block;
  }
}
.application__img--mobile {
  display: inline-block;
  justify-self: center;
}
@media (min-width: 992px) {
  .application__img--mobile {
    display: none;
  }
}
.application .grid {
  grid-template-columns: 1fr;
}
@media (min-width: 992px) {
  .application .grid {
    grid-template-columns: 45% 55%;
  }
}

.ar .grid {
  grid-template-columns: 1fr;
}
@media (min-width: 992px) {
  .ar .grid {
    grid-template-columns: 45% 55%;
    position: relative;
    background: linear-gradient(72.77deg, #EDEEF6 11.72%, rgba(237, 238, 246, 0) 98.52%);
    border-radius: 50px;
    padding: 134px 55px;
  }
}
.ar p {
  color: #868686;
  font-size: 1.5rem;
  line-height: 38px;
}
.ar__text {
  text-align: center;
}
@media (min-width: 992px) {
  .ar__text {
    text-align: left;
  }
}
.ar__img--mobile {
  display: inline-block;
  justify-self: center;
}
@media (min-width: 992px) {
  .ar__img--mobile {
    display: none;
  }
}
.ar__img--desktop {
  display: none;
}
@media (min-width: 992px) {
  .ar__img--desktop {
    display: inline-block;
    position: absolute;
    right: 0;
    top: -100px;
    width: 45%;
  }
}

.socialmedia img {
  justify-self: center;
}
.socialmedia__text {
  color: #000;
  font-weight: 700;
  font-size: 26px;
  line-height: 36px;
  text-align: center;
}
@media (min-width: 992px) {
  .socialmedia__text {
    font-size: 2rem;
    line-height: 57px;
    text-align: left;
  }
}
.socialmedia .grid {
  padding-bottom: 50px;
  grid-template-columns: 1fr;
  border-bottom: 1px solid #DCDEFE;
  margin: 0 15px;
}
@media (min-width: 992px) {
  .socialmedia .grid {
    grid-template-columns: 45% 55%;
    margin: 0 auto;
  }
}

.footer {
  padding: 55px 0;
}
@media (min-width: 992px) {
  .footer {
    padding: 60px 0;
  }
}
.footer .flex {
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 992px) {
  .footer .flex {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__logo p {
  font-size: 0.75rem;
  margin-top: 4px;
}
@media (min-width: 992px) {
  .footer__logo p {
    font-size: 14px;
  }
}
.footer__text {
  margin-top: 25px;
}
@media (min-width: 992px) {
  .footer__text {
    margin-top: 10px;
  }
}
.footer__text p {
  font-size: 12px;
  margin-top: 10px;
}
.btn {
  background-color: #5956E9;
  color: #fff;
  padding: 20px 25px;
  display: inline-block;
  cursor: pointer;
  border: 2px solid transparent;
  text-align: center;
  font-size: 0.9375rem;
  font-weight: 700;
  border-radius: 0.25rem;
  margin: 5px 0;
  transition: background-color 0.2s ease-in-out, border 0.2s ease-in-out, color 0.2s ease-in-out;
}
.btn:hover {
  color: #5956E9;
  background-color: #fff;
  border-color: #5956E9;
}
.btn--small {
  padding: 12px 25px;
}

.btn-menu {
  display: inline-block;
  cursor: pointer;
  z-index: 10;
  border: none;
  background-color: transparent;
}
.btn-menu:hover svg, .btn-menu:hover path {
  stroke: #5956E9;
}
@media (min-width: 992px) {
  .btn-menu {
    display: none;
  }
}

.navigation--desktop {
  display: none;
}
@media (min-width: 992px) {
  .navigation--desktop {
    display: block;
  }
  .navigation--desktop ul {
    display: flex;
  }
  .navigation--desktop a {
    padding: 10px;
    margin: 0 30px;
    color: #252b42;
    line-height: 28px;
    font-weight: 600;
    transition: color 0.1s linear;
  }
  .navigation--desktop a:hover {
    color: #5956E9;
  }
  .navigation--desktop a.active {
    color: #5956E9;
  }
}
.navigation--mobile {
  background-color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  left: 20%;
  bottom: 0;
  z-index: 20;
}

.menu {
  display: flex;
  flex-direction: column;
}
.menu__top {
  padding: 19px 24px;
  border-bottom: 1px solid #5956E9;
}
.menu__top-heading {
  font-size: 40px;
  color: #5956E9;
  font-weight: 700;
}
.menu__list {
  padding: 46px 24px 18px;
  border-bottom: 1px solid #5956E9;
}
.menu__list a {
  color: #252b42;
  display: block;
  margin-bottom: 28px;
  font-weight: 600;
}
.menu__list a:hover {
  color: #5956E9;
}
.menu__list a.active {
  color: #5956E9;
}
.menu__cta {
  padding: 19px 24px;
}